.page-footer {
  padding: 0;
}

.box-registration {
  padding-top:50px ;
  padding-bottom: 50px;

  background-image: linear-gradient(
    rgba(17, 21, 24, 0.8),
    rgba(17, 21, 24, 0.8)), url("../images/footer/mobile-footer-1.png");
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    background-position: center center;
    
  
  @media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
    background-image: url("../images/footer/mobile-footer-1@2x.png");
    
  }

  @media screen and (min-width:768px) {
    padding-bottom: 71px;
    background-image: linear-gradient(
      rgba(17, 21, 24, 0.8),
      rgba(17, 21, 24, 0.8)), url("../images/footer/tablet-footer-2.png");

    @media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
      background-image: url("../images/footer/tablet-footer-2@2x.png");
    }
  }

  @media screen and (min-width:1680px) {
    padding-top:100px ;
    padding-bottom: 62px;
    background-image: linear-gradient(
      rgba(17, 21, 24, 0.8),
      rgba(17, 21, 24, 0.8)), url("../images/footer/desktop-footer-3.png");

    @media screen and (min-device-pixel-ratio: 2), screen and (min-resolution: 192dpi), screen and (min-resolution: 2dppx) {
      background-image: url("../images/footer/desktop-footer-3@2x.png");
    }
  }
}
.registration-footer__title  {
  max-width: 280px;
  margin-top: 0;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
 
  

  color: $primary-background-color;
  font-weight: 800;
  font-size: 14px;
  line-height: 1.36;
  letter-spacing: 0.03em;
  

  @media screen and (min-width:768px) {
    max-width: 688px;
    font-size: 26px;
    line-height: 1.30;
  }

  @media screen and (min-width:1680px){
    max-width: 1090px;
    margin-bottom: 50px;

    font-size: 46px;
  }
}

.footer-form {
  padding: 0;
}

.footer-form__title  {
  max-width: 280px;
  margin-top: 0;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;

  color: $primary-background-color;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.45;
  letter-spacing: 0.08ex;

  @media screen and (min-width:768px) {
    max-width: 452px;
    margin-bottom: 40px;

    font-size: 14px;
    line-height: 1.51;
  }

  @media screen and (min-width:1680px) {
    max-width: 1090px;
    margin-bottom: 56px;

    font-size: 30px;
    line-height: 1.54;
    

    font-size: 30px;
  }
}

.footer-form-text{
  @media screen and (max-width:767px){
    display: inline-block;
    padding: 0;
    margin: 0;
  }
}

.registration-footer-text,
.footer-form-text,
.logo__first {
  color: $accent-color;
}

.logo__first {
  @media screen and (max-width:320px) {
    color: $primary-dark-color;
  }
}

.footer-form__input {
 height: 48px;
 padding-top: 15px;
 padding-bottom: 16px;
 padding-left: 28px;
 background-color:transparent;
 color: $primary-background-color;
 border: 1px solid $primary-background-color;
 margin-left: auto;
 margin-right: auto;
 color: $primary-background-color;

 &::placeholder {
   color:$primary-background-color;

 }

 @media screen and (max-width:319px){
  width: 100%;
  }

  @media screen and (min-width:320px) {
    width: 280px;
    margin-bottom: 50px;
    display: block;

    &:nth-child(2n) {
      margin-bottom: 20px;
    }
  }

  @media screen and (min-width:768px) {
    width: 390px;
    margin-bottom: 31px;
  }

  @media screen and (min-width:1680px){
    width: 478px;
    margin-bottom: 80px;

    &:nth-child(2n) {
      margin-bottom: 30px;
    }
  }
}

.footer-form__input:hover,
.footer-form__input:focus {
  border: 1px solid $accent-color;
  cursor: pointer;
}

.footer-button {
    background-color: transparent;
    border: 2px solid $accent-color;
    width: 274px;
    height: 48px;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.25;
    text-align: center;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: $accent-color;
    padding: 14px;
    cursor: pointer;
    outline: none;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    &:hover,
    &:focus {
      color: $primary-background-color;
      background-color: $accent-color;
    }

    @media screen and (min-width: 1680px) {
    width: 361px;
    height: 70px;
    padding: 26px;
    font-size: 14px;
    line-height: 1.214;
    letter-spacing: 0.14em;
    }
  }

.box-location-footer {
  padding-top: 30px;
  padding-bottom: 30px;

  @media screen and (min-width:768px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @media screen and (min-width:1680px){
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.location-footer-content {
  text-align: center;

  @media screen and (min-width:768px){
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  @media screen  and (min-width:1680px){
    justify-content: flex-start;
  }
}

.location-footer {
  @media screen and (min-width:768px) {
  display: flex;
  align-items: center;
  }
}

.location-footer__link {
  @media screen and (max-width:767px) {
  display: block;
  margin-bottom: 30px;
    &:nth-last-child(1) {
      margin-bottom: 0;
    }
  }
}

.instagram-footer__icon {
  transition: transform .2s;
  @media screen and (min-width:768px) {
    margin-left: 48px;
  }

  @media screen and (min-width:1680px) {
    margin-left:40px;
  }
}

.location-footer__link:hover .instagram-footer__icon,
.location-footer__link:focus .instagram-footer__icon {
  transform: scale(1.5);
}

.footer-phone-text {
  
  color:$primary-dark-color;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.21;
  letter-spacing: 0.08em;
  font-style: normal;

  @media screen and (min-width:768px) {
    font-size: 20px;
    line-height: 1.38;
  }

  @media screen and (min-width:1680px) {
    font-size: 30px;
  }
}


.footer-logo {
  display:inline-block;
  color: $primary-dark-color;
  @media screen and (max-width:767px){
    margin-bottom: 20px;
  }
  
  @media screen and (min-width:768px){
    margin-right: 203px;
  }

  @media screen and (min-width:1680px) {
    margin-right: 290px;
  }
}

.footer-location-text {
  color: $primary-dark-color;
  font-weight: 800;
  font-size: 20px;
  line-height: 1.36;
  letter-spacing: 0.03em;

  @media screen and (min-width: 768px) {
    font-size:26px;
    line-height: 1.23;
  }
}

.phone-footer__icon {
  margin-right: 10px;
  transition: transform .2s;
  width: 20px;
  height: 19.21px;

  @media screen and (min-width:768px) {
    width: 24px;
    height: 23.06px;
  }

  @media screen and (min-width:1680px) {
    width: 30px;
    height: 28.82px;
  }
}

.location-footer__link:hover .phone-footer__icon,
.location-footer__link:focus .phone-footer__icon {
  fill: $accent-color;
  transform: scale(1.5);
}

.footer-logo__icon {
  fill:$accent-color;
  width: 14px;
  height: 20px;

  @media screen and (min-width) {
    width: 22px;
    height: 30px;
  }
}

.logo-under-text {
  font-weight: 800;
  font-size: 6px;
  line-height: 1.66;
  letter-spacing: 0.18em;
  text-transform: uppercase;

  @media screen and (min-width:768px) {
    font-size: 7px;
  }
}

.footer-bottom-box {
  padding-top: 21px;
  padding-bottom: 21px;
  text-align: center;
  background-color: $primary-dark-color;

  @media screen and (min-width:768px) {
    text-align: left;
  }
}

.footer-bottom__text {
  color: $primary-description-color;
  font-weight: 500;
  font-size: 14px;
  line-height: 2;
  letter-spacing: 0;
}


